.ProductGrid {
  background: white;
  color: #000000;
  text-decoration: none;
}

.ProductGrid-thumbnail {
  padding-left: 15px;
  padding-right: 15px;
}

.ProductGrid-detail {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 8px 8px 10px 8px;
}

.ProductGrid-detail h5,
.ProductGrid-price {
  font-size: 0.875em; /* 14px */
}

.ProductGrid-detail h5 {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 60px;
  margin-bottom: 0px;
}



